@import "vars";

.logo {
  width: $logo-width;
  height: $logo-height;
  background-image: url("../images/logo/Air.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  outline: none;
  position: relative;
  z-index: 2;

  &:focus {
    box-shadow: 0 0 1px 1px $pink-active-color;
    border-radius: 2px;
  }
}
