h1,
h2,
h3,
h4,
button {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}

h1 {
  font-size: 64px;
  line-height: 68px;
  letter-spacing: -2px;
}

h2 {
  font-size: 52px;
  line-height: 78px;
}

h3 {
  font-size: 32px;
  line-height: 48px;
}

h4,
%h4 {
  font-size: 13px;
  line-height: 19px;
}

p {
  font-family: "Open Sans", sans-serif;
}
